define('markdown-code-highlighting/helpers/format-markdown', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports.formatMarkdown = formatMarkdown;

  function formatMarkdown(value, options) {
    if(!value || !options){ return; }

    marked.setOptions({
      highlight: function(code) {
        return hljs.highlightAuto(code).value;
      }
    });

    return new Ember['default'].Handlebars.SafeString(window.marked(value));
  }

  exports['default'] = Ember['default'].Handlebars.makeBoundHelper(formatMarkdown);

});