define('ember-cli-pagination/util/safe-get', ['exports', 'ember', '../validate', '../util'], function (exports, Ember, Validate, Util) {

  'use strict';

  exports['default'] = Ember['default'].Mixin.create({
    getInt: function(prop) {
      var raw = this.get(prop);
      if (raw === 0 || raw === "0") {
        // do nothing
      }
      else if (Util['default'].isBlank(raw)) {
        Validate['default'].internalError("no int for "+prop+" val is "+raw);
      }
      return parseInt(raw);
    }
  });

});