define('ember-dropkick/views/dk-select', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  /* global Dropkick */
  exports['default'] = Ember['default'].Select.extend({

    _dk: null,
    settings: {},

    setUpDropKick: function() {
      var self = this,
        oldChange = this.get('settings.change');

      this.set('settings.change', function() {
        self.trigger('change');

        if (typeof oldChange === 'function') {
          oldChange.call(this);
        }
      });
    }.on('init'),

    updateDk: function() {
      if (this._dk !== null) {
        this._dk.select(this.get('value'));
      }
    }.observes('value'),

    createDk: function() {
      this._dk = new Dropkick(this.get('element'), this.get('settings'));
    }.on('didInsertElement'),

    destroyDk: function() {
      if (this._dk !== null) {
        this._dk.dispose();
      }
    }.on('willDestroyElement')

  });

});